@import url(https://fonts.googleapis.com/css2?family=Cardo:wght@700&display=swap);
div#start-page{opacity:0.93;margin:0 auto 0 auto;width:45rem;height:100vh;display:flex;flex-direction:column;justify-content:center}.ui.form .field.invalid input{border-color:red}

.developer-page{display:flex;flex-direction:column;justify-content:center;justify-items:center;align-content:stretch;align-items:center;height:100%;overflow:auto;padding:2rem}.developer-page .form-wrapper{width:30rem}

.ui.fit.loader:after,.ui.fit.loader:before{width:5.571429rem;height:5.571429rem;margin:-1.6rem 0 0 -2.8rem;font-size:2rem}.component,.componentFront,.componentBack{display:inline-block;height:14rem;width:9rem}.componentFront{background:#f1f1f1}.componentBack{background-position:center center;border:none;border-radius:.25rem;box-shadow:0 0 2px 2px rgba(77,77,77,0.7)}.face,.faceBack,.faceFront{padding:1rem;position:relative;height:100%}.faceFront{height:12rem;padding:0;margin:1rem;background:#e1e1e1;border-radius:0.25rem}.content{display:flex;flex-direction:column;height:100%;align-items:center;justify-content:space-around;font-family:"Cardo", serif}.valueFront{font-size:4rem;line-height:4rem}.avatar{overflow:hidden;border-radius:99rem;border:2px solid black}.labelFront{line-height:normal;text-align:center;width:100%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;line-height:1rem}.voterLabel{position:absolute;text-align:center;left:0;right:0;padding:.5rem 0;background:#00000085;color:white}

.dev-card{margin:.5rem;border-radius:.5rem;box-shadow:0 0 1px 1px rgba(77,77,77,0.3);transition:box-shadow 0.15s ease-out}.dev-card:hover{cursor:pointer;box-shadow:0 0 2px 2px rgba(77,77,77,0.6)}.dev-card.selected{background:#01a131}

#po-page{opacity:0.93;padding:2rem;overflow:auto;height:100vh}#po-page .estimation-container{margin-top:2rem;clear:both}#po-page .session-header{font-size:1.5rem;line-height:1.5rem}

.votes-table{position:relative;height:100%;min-height:10rem}.votes-table__cards{display:flex;flex-wrap:wrap;justify-content:space-around}

.style_container__1Hz1V{box-shadow:2px 2px 2px rgba(119,119,119,0.37);border-radius:0.5rem;margin:.5rem;position:relative}

.tab-container.ui.attached.segment{padding:0;border:none}

#particles-js {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgb(4,97,120);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: -1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

body {
  margin: 0;
}

#root{
  height: 100vh;
}

